import React,{useState} from 'react'
function App() {
  const [text,setText]=useState('');
  const [item,setItem]=useState([]);
  function setInput(event)
  {
  const TakeInput=event.target.value;
  setText(TakeInput);
  }
  function ShowItem(){
    setItem((Previous)=>(
      [...Previous,text]
    ))
    setText('');
  }

  return (
    <div>
      <h1>Todo List</h1>
      <input type='text' onChange={setInput} value={text}/>
      <button onClick={ShowItem}>ADD</button>
      <ul>
        {
          item.map((ele)=>(
            <li>{ele}</li>
          ))
        }
      </ul>
    </div>
  )
}

export default App



